<template lang="pug">

div
	Title(t="Becas de fotocopias" i="content-copy")
	transition(name="slide-fade" mode="out-in")
		.container-card(v-if="!$store.state.loading")
			span.sectitle.text-ink Cronograma
			.cal-beca
				.refs
					.ref.ref2 Entrega
					.ref.ref5 Inscripción
					.ref
					.ref.ref8 Feriado
					.ref.ref7 Veda electoral
					.ref.ref9 Elecciones CEI
				.month(v-for="(d,i) in offsets")
					Mes( :m="i+3" y="19" :o="d" :r="refs[i]" )
			span(style="display:block; text-align:center; margin-top: .5rem") Para inscribirte solo tenés que acercarte por la oficina de la Franja, ¡te esperamos!
	transition(name="slide-fade" mode="out-in")
		.container-card.results(v-if="!$store.state.loading")
			span.sectitle.text-ink Resultados
			span.secsubtitle.text-ink90
				|Entrega desde el 
				b {{data['data']['start']}} 
				|al 
				b {{data['data']['end']}}
			b-table(:data="data['table']" :columns="columns" :mobile-cards="false").rsTable

</template>
<style lang="stylus">

.sectitle
	margin: .5rem 1rem 1rem
	line-height: 1.2
	display: block
	font-size: 2rem
.secsubtitle
	margin: -1rem 1rem 1rem
	display: block
	font-size: 1.1rem

	
</style>
<script>
import axios from 'axios'

import Title from '@/components/Title.vue'
import Mes from '@/components/Mes.vue'

export default {
	name: 'fotocopias',
	components: {
		Title,
		Mes
	},
	data() {
		return{
			refs: [
				[0,8,0,0,0,0,0,0,0,0,4,5,0,0,5,5,6,8,8,0,0,1,2,2,2,3,0,0,4,5],
				[8,5,6,0,0,1,2,2,2,3,0,0,4,5,5,5,6,0,0,1,2,2,2,3,8,0,4,5,5,5,6],
				[0,0,1,2,2,2,3,0,0,7,7,9,8,4,0,0,8,5,5,8,6,0,0,1,2,2,3,0,0,0]
			],
			offsets: [ 1, 3, 6 ],
			data : [],
			columns: [
									{
										field: 'name',
										label: 'Nombre',
										
										
									},
									{
										field: 'ld',
										label: 'Terminación DNI',

										numeric: true
									},
									]
		}
	},
	beforeMount(){
		this.$store.commit('startLoading')
	},
	mounted() {
		axios
			.get("http://franja.xyz/ganadores.json")
			.then(response => (this.data = response.data, this.$store.commit('stopLoading')))

	}
}

</script>
<style lang="stylus">

@import "../assets/styles/variables.styl";

