			.spacer
				opacity: 0
			width: 10.5rem
			height: 12rem
			color: color-white
		
		.month, .refs
			display: inline-block
			vertical-align: top
			margin: 0 12px
			.ref1b
				background: color-main
				.triangle
					width: .66rem;
					height: .66rem;
					display:inline-block
					margin: 0 .25rem 0 .33rem
