			.ref8
				background: rgba(#7F7F7F, 0.25)
				color: #E02323 !important
			.ref9
				background: color-main !important
			[class^="ref"]
				color: color-white
				text-shadow: 0 1px 1px rgba(#000, 0.1)
		
